<template>
  <div class="home">
    <div class="banner">
      <Banner :banner="banner"></Banner>
    </div>
    <linkList></linkList>
    <Gray></Gray>
    <!--  -->
    <Bulletin :bulletin="bulletin"></Bulletin>
    <Gray></Gray>
    <!--  -->
    <CheerStation :cheer="cheer"></CheerStation>
    <Gray></Gray>
    <!--  -->
    <Leature :lecture="lectureArr"></Leature>
    <Gray></Gray>
    <Company :company="company" v-if="company.length > 0"></Company>
    <!--  -->
    <JobList :jobArr="jobArr"></JobList>
    <Gray></Gray>
    <!--  -->
    <mt-loadmore :bottomMethod="loadBottom" :bottomAllLoaded="allLoaded" ref="loadmore" bottomPullText="" :maxDistance="maxDistance">
    <listTitle :listTitle="listTitle" ></listTitle>
    <Article ref="Article" :adPass="adPass" :articles="articles"></Article>
    <loadMore :title="loadTitle"></loadMore>
    </mt-loadmore>
  </div>
</template>

<script>
  import Banner  from '../../components/banner/banner';
  import linkList from '../../components/common/linkList'
  import Bulletin from '../../components/common/bulletin'
  import CheerStation from '../../components/cheerstation/cheer'
  import Company from '../../components/job/company'
  import JobList from '../../components/job/jobList'
  import Article from '../../components/article/article'
  import LoadMore from '../../components/common/loadMore'
  import listTitle from '../../components/common/listTitle'
  import Leature from '../../components/lecture/lecture'

  export default {
    data() {
      return {
        banner: [],
        cheer: [],
        adPass: [],
        bulletin: [],
        company: [],
        jobArr: [],
        lectureArr: [],
        swBl: true,
        allLoaded: true,
        newsPage: 1,
        swBl: true,
        loadTitle: '',
        maxDistance: 100,
        pages: 0,
        windowHeight: 0,
        articles: [],
        listTitle: {
          title: 'IT',
          arr: [
            {
              msg: '',
              url: this.$store.state.domain + '/blog/'
            },
            {
              msg: '',
              url: this.$store.state.domain + '/news/'
            }
          ]
        }
      }
    },
    created () {
      this.getData();
    },
    mounted () {
      // 
      this.getNews ()
      this.windowScroll ();
    },
    methods: {
      getData() {
        this.$post('/api/video/indexAdvertiement').then((res) => {
          if (res.code == 200) {
              this.banner = res.data.carousel
              this.cheer = res.data.focus ? res.data.focus : []
              this.adPass = res.data.pass
          }
        }).catch((err) => {
          throw err
        })
        this.$get(this.$store.state.domain + "/api/video/indexExpress").then(res => {
          this.bulletin = res.data
        }).catch(err => {
          throw err
        })
        this.$get("/api/video/ztcJob").then((res) => {
          if (res.code == 200) {
            this.company = res.data.company
            this.jobArr = res.data.job
          }
        }).catch(err => {
          throw err;
        })
        this.$post('/api/video/indexVideo').then((res) => {
          if (res.code == 200) {
            this.lectureArr = res.data
          }
        }).catch((err) => {
          throw err
        })
      },
      loadBottom () {
        if (this.swBl) {
          this.newsPage += 1
          this.getNews(this.newsPage)
          this.swBl = false
        }
      },
      getNews (page) {
        this.$forceUpdate()
        this.$get(this.$store.state.domain + '/api/video/news/', {page: page ?  page : this.newsPage} ).then((res) => {
          this.allLoaded = true
          if (res.code == 200) {
            this.swBl = true
            this.pages = res.data.pages
            if (page) {
              this.articles = this.articles.concat(res.data.dataList)
              this.windowScroll ()
              if(page === res.data.pages) {
                this.allLoaded = true
                this.loadTitle = ''
              }
              this.$refs.loadmore.onBottomLoaded()
            }else {
              this.articles = res.data.dataList
            }
          }
        }).catch((err) => {
          this.allLoaded = true
          throw err
        })
      },
      windowScroll () {
        var _this = this
        window.onscroll = function () {
          //scrollTop
          var scrollTop = document.documentElement.scrollTop||document.body.scrollTop
          //windowHeight
          var windowHeight = document.documentElement.clientHeight || document.body.clientHeight
          //scrollHeight
          var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight
          //
          if(scrollTop + windowHeight >= (scrollHeight - 10) && _this.pages !== _this.newsPage){
            _this.allLoaded = false
          }else {
            _this.allLoaded = true
          }
        }
      }
    },
    components: {
      Banner, linkList, Bulletin, CheerStation, Company, JobList, Article, LoadMore, listTitle, Leature
    }
  }
</script>

<style lang="less">
  .home {
    overflow-y: scroll;
    .banner {
      height: 3.46rem;
      width: 7.5rem;
      overflow: hidden;
      .mint-swipe-item {
        height: 3.46rem;
      }
      img {
        height: 3.46rem;
        width: 7.5rem;
        display: block;
      }
    }
  }
  .mint-loadmore {
    width: 7.5rem;
    background: #fff;
  }
</style>


